      }
    },
    methods: {
      createPlaylist() {
        if (sessionStorage.getItem('playlist')?.length) {
          const currentSong = JSON.parse(sessionStorage.getItem('playlist'))

          console.log('current', currentSong)
          const newPlaylist = [...currentSong, this.song]
          localStorage.setItem('playlist', JSON.stringify(newPlaylist))
        } else {
          localStorage.setItem('playlist', JSON.stringify([this.song]))
        }
      },
      cleanPlaylist() {
        localStorage.removeItem('playlist')
      },
      addPlaylist() {
        this.$store.commit('addPlaylist', this.item)
      }
    }
  }
</script>

